// #region
.wrapper {
	position: relative;
	max-width: $maxWidth;
	min-height: 100vh;
	margin: 0 auto;
}

[class*='_container'] {
	/* border: groove; */

	box-sizing: content-box;
	max-width: $maxWidthContainer;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

// === Сетка ===
// [class^='grid-'] {
